

































































































































































































.ratings_wrap{
  position: absolute;
  top: 3.5rem;
  overflow: hidden;
  bottom:0;
}
.rating_head {
  overflow: hidden;
  margin-top: 0.38rem;
  padding-bottom: 0.7rem;
  margin-bottom: 0.36rem;
  background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
  background-size: 100% auto;
}

.rating_head .rating_count {
  width: 2.76rem;
  float: left;
  text-align: center;
  border-right: 1px solid #e6e7e8;
  height: 1.4rem;
}

.rating_head .service_rating {
  width: 4.32rem;
  float: right;
  height: 1.4rem;
}

.rating_count p:first-child {
  height: 0.58rem;
  line-height: 0.58rem;
  font-size: 0.48rem;
  color: #ff9900;
}

.rating_count p:nth-child(2) {
  height: 0.37rem;
  line-height: 0.37rem;
  font-size: 0.24rem;
  color: #07111b;
}

.rating_count p:nth-child(3) {
  height: 0.33rem;
  line-height: 0.33rem;
  font-size: 0.2rem;
  color: #93999f;
}

.service_rating p {
  overflow: hidden;
  height: 0.36rem;
  margin-bottom: 0.1rem;
  line-height: 0.36rem;
  font-size: 0.24rem;
  color: #07111b;
}

.service_rating p span {
  color: #ff9900;
  vertical-align: middle;
}

.service_rating p:last-child span {
  color: #93999f;
  margin-left: 0.2rem;
  vertical-align: top;
}

.service_rating div {
  display: inline-block;
  margin: 0 0.2rem;
}

.rating_box {
  width: 100%;
  overflow: hidden;
}

.shoprating_lists {
  padding: 0 0.35rem;
}

.shoprating_lists li {
  padding: 0.35rem 0;
  border-bottom: 1px solid #e6e7e8;
  overflow: hidden;
}
.shoprating_lists li:last-child{
  margin-bottom: 10px;
}
.shoprating_lists img {
  width: 0.57rem;
  height: auto;;
  float: left;
  border-radius: 0.285rem;
  margin-right: 0.2rem;
}

.shoprating_lists .ra_right {
  width: 6rem;
  float: right;
}

.ra_right p {
  overflow: hidden;
  font-size: 0.24rem;
  line-height: 0.36rem;
  color: #07111b;
}

.ra_right p:first-child {
  font-size: .2rem;
}

.ra_right span {
  font-size: 0.2rem;
  vertical-align: middle;
  color: #93999f;
}

.ra_right p:first-child span {
  float: right;
  vertical-align: top;
}

.ra_right a {
  font-size: 0.18rem;
  display: inline-block;
  padding: 0.02rem 0.12rem;
  border: 0.01px solid #e6e7e8;
  color: #93999f;
  margin-right: 0.12rem;
  margin-top: 0.1rem;
}





