







































































































































































































.fooddetail {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 7.5rem;
  bottom: 0.96rem;
  background: #fff;
  overflow: hidden;
}

.food_image {
  width: 100%;
  padding-top: 100%;
  position: relative;
  margin-bottom: 0.28rem;
}

.food_image img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.food_deta_box {
  height: 1.89rem;
  position: relative;
  padding: 0 0.35rem;
  padding-bottom: 0.36rem;
  background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
  background-size: 100%;
}

.fooddetail h1 {
  height: 0.45rem;
  line-height: 0.45rem;
  font-size: 0.28rem;
  font-weight: 700;
  color: rgb(7, 17, 27);
}

.fooddetail p.food_com {
  height: 0.3rem;
  line-height: 0.3rem;
  font-size: 0.2rem;
  color: rgb(147, 153, 159);
  margin-bottom: 0.35rem;
}

p.food_com span {
  margin-right: 0.2rem;
}

.add_car {
  position: absolute;
  width: 1.46rem;
  height: 0.48rem;
  border-radius: 0.24rem;
  display: block;
  line-height: 0.48rem;
  text-align: center;
  color: #fff;
  background: rgb(0, 160, 220);
  right: 0.36rem;
  bottom: 0.8rem;
  z-index: 10;
}

.fooddetail .add_box {
  position: absolute;
  right: 0.45rem;
  bottom: 0.8rem;
}

.opac-transition {
  opacity: 1;
  transition: 0.4s all;
}

.opac-enter, .opac-leave {
  opacity: 0;
}

.transmove-transition {
  left: 0;
  transition: 0.5s all
}

.transmove-enter, .transmove-leave {
  left: 100%;
}

.detail_close {
  position: absolute;
  display: block;
  width: 0.4rem;
  height: 0.4rem;
  top: 0.4rem;
  left: 0.2rem;
  font-size: 0.45rem;
  color: #fff;
  -webkit-transform: scaleY(1.4);
          transform: scaleY(1.4);
  z-index: 10;
  font-weight: bold;
}

.food_intro {
  padding: 0.3rem 0.36rem;
  padding-bottom: 0.72rem;
  background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
  background-size: 100%;
}

.food_intro h2, .food_rating h2 {
  height: 0.46rem;
  line-height: 0.46rem;
  font-size: 0.3rem;
  color: #000;
  font-weight: normal;
}

.food_rating h2 {
  margin: 0.26rem 0.36rem;
}

.food_intro p {
  line-height: 0.48rem;
  color: rgb(77, 85, 93);
  font-size: 0.24rem;
}

.rating_lists {
  padding: 0 0.35rem;
}

.rating_text {
  height: 0.25rem;
}

span.high {
  background: url("../../common/img/finger_03.jpg") left center no-repeat;
  display: inline-block;
  vertical-align: bottom;
  height: 0.2rem;
  width: 0.23rem;
  margin-right: 0.2rem;
  background-size: 100% auto;
}

span.highO {
  background: url("../../common/img/finger_07.jpg") left center no-repeat;
  background-size: 100% auto;
}

.rating_lists li {
  padding: 0.3rem 0;
  border-bottom: 1px solid #e6e7e8;
}

.rating_lists p:first-child {
  overflow: hidden;
  margin-bottom: 0.18rem;
}

.rating_lists p:first-child span {
  float: right;
}

.rating_lists p:first-child img {
  margin: 0 0.27rem;
  vertical-align: bottom;
}

.rating_lists li p:first-child {
  height: 0.3rem;
  line-height: 0.3rem;
  font-size: 0.2rem;
  color: #93999f;
}

.rating_lists img {
  width: 0.23rem;
  height: auto;
}



