




























































































































































.goods_wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 3.48rem;
  left: 0;
  width: 100%;
  bottom: 0.96rem;
  overflow: hidden;
}

.menu {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 1.6rem;
          flex: 0 0 1.6rem;
  background: #f3f5f7;
  width: 1.12rem;
}

.content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.menu li {
  height: 1.08rem;
  line-height: 0.28rem;
  font-size: 0.24rem;
  padding: 0 0.24rem;
  color: #333;
  display: table;
  width: 1.12rem;
  border-bottom: 0.01rem solid rgba(7, 17, 21, 0.1);
}
.menu li:last-child{
  border: none;
}
.menu li.current{
  background:#fff;
  font-size:0.24rem;
  border-bottom:none;
  height: 1.09rem;
}
.menu span {
  display: table-cell;
  vertical-align: middle;
}
.big_item h1{
  height:0.52rem;
  line-height:0.52rem;
  padding-left: 0.24rem;
  background: #f3f5f7;
  font-size:0.24rem;
  color: rgb(147,153,159);
  border-left: 0.04rem solid #d9dde1;
}
.small_item{
  padding:0 0.36rem;
}
.small_item_li{
  padding: 0.362rem 0;
  border-bottom:1px solid #d9dde1;
  position: relative;
  overflow: hidden;
}
.small_item_li:last-child{
  border:none;
}
.small_item_li img{
  float:left;
  width:1.14rem;
  height:1.14rem;
  margin-right: 0.2rem;
}
.small_item_li strong{
  display: block;
  height:0.36rem;
  line-height:0.36rem;
  font-size:0.28rem;
  color: rgb(7,17,27);
}
.small_item_li p{
  height:0.37rem;
  line-height:0.4rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width:3.1rem;
  color: rgb(147,153,159);
  font-size:0.2rem;
}
.small_item_li span{
  margin-right: 0.24rem;
}
.new_pri{
  font-size:0.28rem;
  color: rgb(240,20,20);
  font-weight:bold;
}
.old_pri{
  font-size:0.2rem;
  font-weight:bold;
  vertical-align: top;
  color: rgb(147,153,159);
}



