#app.filter{
  -webkit-filter: blur(0px);
  -moz-filter: blur(0px);
  -o-filter: blur(0px);
  filter: blur(0px);
}
.header{
  padding:0.48rem 0.24rem 0 0.24rem;
  height:2.2rem;
  background: #494748;
  position: relative;
  z-index:10;
  width:7.02rem;
}
.avatar{
  display: inline-block;
  width:1.28rem;
  height:1.28rem;
  margin-right:0.32rem;
  margin-left: 0.24rem;
}
.avatar img{
  width:100%;
  height:100%;
}
.header_right{
  display: inline-block;
  width:5rem;
  vertical-align: top;
}
.seller_name{
  font-size:0.32rem;
  font-weight: bold;
  margin-bottom: 0.1rem;
  height: 0.4rem;
  line-height: 0.4rem;
  color: #fff;
}
.seller_name img{
  width:0.6rem;
  height:0.36rem;
  vertical-align: top;
  margin-right: 0.12rem;
}
.dispatch_type{
  height:0.36rem;
  line-height:0.36rem;
  color: #fff;
  font-size:0.24rem;
}
.supports{
  font-size:0.2rem;
  color: #fff;
  height:0.5rem;
  line-height:0.5rem;
  color: #fff;
  position: relative;
}
.supports label{
  display: block;
  position: absolute;
  width:0.85rem;
  height:0.5rem;
  line-height:0.5rem;
  text-align: center;
  border-radius:0.1rem;
  background: rgba(0,0,0,0.2);
  right:0;
  bottom:0;
}
.bulletin{
  height:0.56rem;
  line-height: 0.56rem;
  position: absolute;
  width: 6.5rem;
  left:0;
  bottom:0;
  padding: 0 0.24rem;
  padding-left: 0.76rem;
  color: #fff;
  font-size:0.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  background: url("../../common/img/bulletin@3x.png") no-repeat;
  background-color:rgba(7,17,27,0.2);
  background-position: 0.24rem 0.16rem;
  background-size:0.44rem auto ;
}
.icon img{
  width:0.24rem;
  height:0.24rem;
  margin-right:0.08rem;
  vertical-align: middle;
}
.info{
  width:100%;
  height:100%;
  position: fixed; top:0;
  left:0;
  z-index: 100;
  background: rgba(0,0,0,0.8);
}
.info_main{
  height:70%;
  text-align:center;
  overflow-y: auto;
  padding-top: 1.18rem;
  overflow-x: hidden;
}
.info_main h1{
  height:0.6rem;
  line-height:0.6rem;
  color: #fff;
  font-size:0.32rem;
  font-weight:bold;
}
.close{
  font-size:0.4rem;
  width:0.4rem;
  height:0.4rem;
  display: block;
  position: absolute;
  bottom:0.75rem;
  color: #999da1;
  left:3.56rem;
}
.info_main h2{
  height:0.96rem;
  font-size:0.24rem;
  line-height:0.96rem;
  font-weight:bold;
  color: #fff;
}
.support_deta{
  margin-bottom: 0.3rem;
}
.support_deta li{
  padding: 0 0.72rem;
  text-align: left;
  height:0.5rem;
  font-size:0.24rem;
  color: #fff;
  line-height:0.5rem;
}
.support_deta img{
  vertical-align: middle;
  width:0.32rem;
  height:auto;
  margin-right: 0.12rem;
}
.info p{
  padding: 0.06rem 0.9rem 0 0.9rem;
  font-size:0.24rem;
  line-height:0.48rem;
  color: #fff;
  text-align: left;
}



