













































.star_box_1{
  margin-top:0.2rem;
}
.star_box_1 .star_item{
  width:0.4rem;
  height:0.38rem;
  display: inline-block;
  margin-right: 0.41rem;
}
.star_box_2 .star_item{
  width:0.3rem;
  height:0.3rem;
  display: inline-block;
  margin-right: 0.13rem;
  vertical-align: middle;
}
.star_box_3{
  display: inline-block;
}
.star_box_3 .star_item{
  width:0.2rem;
  height:0.2rem;
  display: inline-block;
  margin-right: 0.07rem;
  vertical-align: middle;
}
.star_item:last-child{
  margin-right: 0;
}
.star_item.on{
  background:url("../common/img/star48_on@3x.png") left top no-repeat;
  background-size:100% auto;
}
.star_item.off{
  background:url("../common/img/star48_off@3x.png") left top no-repeat;
  background-size:100% auto;
}
.star_item.half{
  background:url("../common/img/star48_half@3x.png") left top no-repeat;
  background-size:100% auto;
}



